// styling
function adjustSteeringWheel() {
    const steeringWheelContainer = document.getElementById('steeringWheelContainer');
    let size = 0;

    // Determine the size based on the smaller of the viewport's width and height
    if (window.innerWidth < window.innerHeight) {
        size = window.innerWidth * 0.5; // Use 50% of width if width is smaller
    } else {
        size = window.innerHeight * 0.5; // Use 50% of height if height is smaller
    }

    // Apply the determined size to the steering wheel container
    steeringWheelContainer.style.width = `${size}px`;
    steeringWheelContainer.style.height = `${size}px`; // Maintain aspect ratio

    // Ensure only half of the steering wheel is visible
    steeringWheelContainer.style.bottom = `${size / 2}px`; // Adjust bottom position
}

// Adjust the steering wheel size and position on window resize
window.addEventListener('resize', adjustSteeringWheel);

// Initial adjustment
adjustSteeringWheel();

